export interface GiftPlan {
    id: number;
    merchant_id: number;
    name: string;
    icon: string;
    original_price: number;
    price: number;
    cycle: GitfPlanCycle;
    life_time: number;
    life_days: number;
    detail: string[];
    status: GitfPlanStatus;
    transable: boolean;
    refund_policy: GitfPlanRefundPolicy;
    date_created: Date;
    date_updated: Date;
    date_deleted: Date;
}

export const getGiftplanList = async ({ page, pageSize, data = {} }: any) => {
    return ((await request({
        url: '/giftplan/list',
        method: 'get',
        params: {
            page,
            pageSize,
            data
        }
    })).data) as IpageDataDto<GiftPlan>
}

